<template>
	<Table
		v-if="showTable"
		:data="data"
		:row-class-name="disabled ? 'global-disabled-row-table' : ''"
		:load="onExpandChange"
		:tree-props="
			!disabled && {
				children: 'child',
				hasChildren: 'has_child'
		}"
		row-key="id"
		lazy>
		<TableColumn width="50"></TableColumn>
		<TableColumn width="100" prop="is_enable">
			<template #header>
				<Checkbox
					v-if="category"
					v-model="category.is_enable"
					type="toggle"
					:value="1"
					:value-off="0"
					:disabled="disabled"
					@input="updateBuybackDeviceByCategory()"></Checkbox>
			</template>
			<template #default="scope">
				<Checkbox
					v-model="scope.row.is_enable"
					class="py-3"
					type="toggle"
					:value="1"
					:value-off="0"
					:disabled="disabled"
					:loading="scope.row.is_loading"
					@input="toggleEnable(scope.row)"/>
			</template>
		</TableColumn>
		<TableColumn
			label="Brand"
			v-if="route.params.brand === 'all'" 
		>
			<template #default="scope">
				<div class="flex items-center gap-2">
					<span>{{ scope.row.manufacturer }}</span> 
					<Tooltip
						v-if="
							scope.row.has_condition === 0 ||
							scope.row.has_price === 0"
						placement="top">
						<template #default>
							<i class="material-icons text-orange-04">warning</i>
						</template>
						<template #content>
							<div class="w-80 leading-6">
								<p
									v-if="
										(scope.row.has_condition === 0 && scope.row.has_price === 0) ||
										(scope.row.has_condition === 0 && scope.row.has_price === 1)
									">
									To start buying this item, switch the "Activate" toggle to on, then configure its conditions and pricing.
								</p>
								<p v-else>To start buying this item, you must configure its pricing.</p>
							</div>
						</template>
					</Tooltip>
				</div>
			</template>
		</TableColumn>
		<TableColumn prop="name" label="Model">
			<template #default="scope">
				{{ scope.row?.name ?? '-' }}
			</template>
		</TableColumn>
		<TableColumn label="Purchasing Type" :min-width="300">
			<template #default="{ row }">
				<div class="flex flex-wrap items-center py-2">
					<div class="flex flex-wrap items-center mr-4">
						<Checkbox
							:modelValue="row.instore == 2 ? 0 : row.instore"
							:indeterminate="row.instore == 2"
							:value="1"
							:value-off="0"
							:disabled="!row.is_enable || disabled || row.is_loading"
							@input="
								(newValue) => updateBuybackType(row, newValue, 'instore')
							">
							<span class="ml-1 font-normal">In-Store</span>
						</Checkbox>
					</div>
					<div class="flex flex-wrap items-center">
						<Checkbox
							:modelValue="row.mailin == 2 ? 0 : row.mailin"
							:indeterminate="row.mailin == 2"
							:value="1"
							:value-off="0"
							:disabled="!row.is_enable || disabled || row.is_loading"
							@input="(newValue) => updateBuybackType(row, newValue, 'mailin')">
							<span class="ml-1 font-normal">Mail-in</span>
						</Checkbox>
						<div class="relative ml-6">
							<transition name="fade">
								<Loading v-show="loading.buybackType.update.get(row.id)" />
							</transition>
						</div>
					</div>
					<div
						v-if="row.is_enable"
						class="flex flex-row items-center justify-center ml-auto md:mr-20 lg:opacity-0 group-hover:opacity-100 transition-opacity">
						<a
							v-if="!row.has_child"
							:href="`/setting/bulk-price-editor/${route.params.category}/${route.params.brand === 'all' ? row.manufacturer : paramsBrand}?ref=true`"
							:disabled="row.is_loading"
							class="btn btn-teal btn-dense btn-filled"
							@click.prevent="showPricing(row, 1)">
							<div class="flex items-center justify-center">
								<i class="material-icons text-lg">attach_money</i>
								Pricing Editor
							</div>
						</a>
						<a
							v-else
							:href="`/setting/bulk-price-editor/${route.params.category}/${route.params.brand === 'all' ? row.manufacturer : paramsBrand}?ref=true&model=${row.name}`"
							:disabled="row.is_loading"
							class="btn btn-teal btn-dense btn-filled"
							@click.prevent="showPricing(row, 2)">
							<div class="flex items-center justify-center">
								<i class="material-icons text-lg">attach_money</i>
								Pricing Editor
							</div>
						</a>
					</div>
				</div>
			</template>
		</TableColumn>
	</Table>
	<div
		v-else
		class="w-full bg-teal-01 bg-opacity-10"
		style="height: 540px;"
	/>
	
	<Modal size="sm" v-model="modal">
		<template #header>
			Important Information
		</template>
		<template #default>
			<div v-if="selectedRow" class="flex flex-col items-center">
				<div class="flex justify-center items-center p-8 rounded-full bg-teal-lighten">
					<img src="/icon/settings-icon/catalog-not-found.svg" alt="">
				</div>
				<p class="text-center mt-8">Your item has been enabled in your catalog, but you still need to complete the following steps</p>
				<p class="text-center my-6">Here are your next steps:</p>
				<div class="flex flex-col gap-6">
					<div class="flex items-center gap-4">
						<div class="flex items-center justify-center bg-teal-02 rounded-full text-white font-semibold w-8 h-8">1</div>
						<p class="text-teal-03">Set item conditions as needed within the 
							<a class="underline text-black" :href="`/setting/product-conditions/create?model=${findParentName(selectedRow)}&ref=true`">Conditions Settings</a>
						</p>
					</div>
					<div class="flex items-center gap-4">
						<div class="flex items-center justify-center bg-teal-02 rounded-full text-white font-semibold w-8 h-8">2</div>
						<p
						class="text-teal-03">
						Set item prices as needed within the 
						<a
						v-if="!selectedRow?.is_child"
						class="underline text-black" 
						:href="`/setting/bulk-price-editor/${route.params.category}/${route.params.brand === 'all' ? selectedRow?.manufacturer : paramsBrand}?ref=true`"
						@click.prevent="showPricing(selectedRow, 1)"
						>
						Price Editor Settings
						</a>
						<a
						v-else
						class="underline text-black"
						:href="`/setting/bulk-price-editor/${route.params.category}/${route.params.brand === 'all' ? selectedRow?.manufacturer : paramsBrand}?ref=true&model=${selectedRow?.name}`"
						@click.prevent="showPricing(selectedRow, 2)"
						>
						Price Editor Settings
						</a>
						</p>
					</div>
				</div>
			</div>
		</template>
		<template #footer>
		<div class="flex justify-between items-center">
			<div>
				<Checkbox
				v-model="dontShow"
				:value="1"
				:value-off="0"
				class="font-normal"
				>
					Don't show this again for this session
				</Checkbox>
			</div>
				<Button color="teal" view="smooth" @click="closeModal"><i class="material-icons mr-2">close</i>Close Notification</Button>
		</div>
		</template>
	</Modal>
</template>

<script>
import { computed, defineComponent, reactive, ref, watch, onMounted, markRaw } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { useUser } from 'composable/user'

import { useBuybackDevice } from 'composable/setting'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import Loading from 'bbUi/components/loading/loading.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import Button from 'bbUi/components/button/button.vue'

import { titleCase } from 'utils/string'

export default defineComponent({
	name: 'DeviceTable',
	components: {
		Checkbox,
		Loading,
		Modal,
		Button
	},
	props: {
		data: Array,
		category: {
			type: Object,
			default: {
				name: null,
				is_enable: 0,
				is_mac: 0
			}
		},
		disabled: {
			type: Boolean,
			default: false
		}
	},
	emits: ['update-buyback', 'update-all', 'update-buyback-category'],
	setup(props, { emit }) {
		const showTable = ref(true)
		const route = useRoute()
		const router = useRouter()
		const { me } = useUser()
		const { actionUpdateBuybackType, actionGetChildren } = useBuybackDevice()
		const dontShow = ref(0)
		const loading = reactive({
			buybackType: {
				update: new Map()
			}
		})
		const category = reactive({
			name: props.category.name,
			is_enable: props.category.is_enable,
			is_mac: props.category.is_mac
		})

		const data = ref(props.data.map(item => ({
				...item,
				child: item.child.map(itemchild => ({
					...itemchild,
					is_child: true,
					manufacturer: item.manufacturer
				}))
			})
		) || [])

		const modal = ref(false)
		const selectedRow = ref(null)
		const modalIdentifier = 'buyback-setup-warning'

		const flattenData = computed(() => flatten(data.value))
		const paramsBrand = computed(() => {
			const brands = route.params.brand.split('')
			brands[0] = brands[0].toUpperCase()
			return brands.join('')
		})

		const toggleEnable = (row) => {
			if (row.is_enable === 1) {
				if (row.has_condition === 1 && row.has_price === 1) {
					updateBuybackDevice(row)
				} else {
					updateBuybackDevice(row)	
					selectedRow.value = row
					if (shouldShowModal()) {
						modal.value = true
					}
				}
			} else {
				updateBuybackDevice(row)
			}
		}

		const findParentName = (row) => {
			if (row?.parent_id) {
				const parent = data.value.find(x => x.id === row?.parent_id);
				return parent ? parent?.name : row?.name;
			}
			return row?.name;
		}

		const shouldShowModal = () => {
			return sessionStorage.getItem(modalIdentifier) !== '1'
		}

		const closeModal = () => {
			if (dontShow.value === 1) {
				sessionStorage.setItem(modalIdentifier, '1')
			}
			modal.value = false
		}

		const updateBuybackDevice = (device) => {
			device.is_loading = true

			device.instore = device.is_enable
			device.mailin = device.is_enable
			const parent = device.parent?.total_active ?? device.total_active

			emit(
				'update-buyback',
				{
					...device,
					...(!category.is_mac && { total_active: parent.total_active }),
					is_mac: category.is_mac
				},
				(res) => {
					if (res.status_code != 200) {
						device.is_enable = device.is_enable == 1 ? 0 : 1
						device.instore = device.is_enable
						device.mailin = device.is_enable
						device.is_loading = false
						return
					}

					const isParent = device.child.length > 0
					if (isParent) {
						updateBuybackTypeChildRow(device.child, 'all', device.is_enable)
					}

					const flattenItem = flattenData.value.get(device.id)
					if (flattenItem) {
						if (flattenItem.parents)
							updateBuybackStatusParentRow(flattenItem.parents, res)
					}

					if (device.total_active) {
						device.total_active = res.data.total_active_category
					}

					device.is_loading = false
				}
			)
		}
		const updateBuybackDeviceByCategory = () => {
			emit('update-buyback-category', category, (err) => {
				if(err) category.is_enable = category.is_enable == 1 ? 0 : 1
			})
		}
		const updateBuybackTypeChildRow = (childrens, type='all', value) => {
			if (!childrens) return
			for (let child of childrens) {
				if(type === 'all'){
					child.is_enable = value
					child.instore = value
					child.mailin = value
				}else {
					child[type] = value
				}

				const isDisabledBothInstoreMailin =
					child.instore === 0 && child.mailin === 0
				if (isDisabledBothInstoreMailin) child.is_enable = 0
				else child.is_enable = 1

				const hasChild = child.child.length > 0
				if (hasChild) updateBuybackTypeChildRow(child.child, type, value)
			}
		}
		const updateBuybackTypeParentRow = (parents, type) => {
			for (let parent of parents) {
				const hasChild = parent.child.length > 0
				if (hasChild) {
					// Update In-Store and Mail-In checked status on parents child
					const { buybackType, enableStatus } = parent.child.reduce(
						(curr, next) => {
							if (next[type] === 0) curr.buybackType.isCheckAll = false
							if (next[type] === 1) curr.buybackType.isUncheckAll = false	
							if (
								(!curr.buybackType.isCheckAll &&
									!curr.buybackType.isUncheckAll) ||
								next[type] === 2
							)
								curr.buybackType.isIndeterminate = true
							if (next.is_enable === 0) curr.enableStatus.isEnableAll = false
							if (next.is_enable === 1) curr.enableStatus.isDisableAll = false
							return curr
						},
						{
							buybackType: {
								isCheckAll: true,
								isUncheckAll: true,
								isIndeterminate: false
							},
							enableStatus: { isEnableAll: true, isDisableAll: true }
						}
					)
					if (buybackType.isCheckAll) parent[type] = 1
					if (buybackType.isUncheckAll) parent[type] = 0
					if (buybackType.isIndeterminate) parent[type] = 2
					if (enableStatus.isEnableAll) parent.is_enable = 1
					if (enableStatus.isDisableAll) parent.is_enable = 0
				}
			}
		}
		const updateBuybackStatusParentRow = (parents) => {
			for (let parent of parents) {
				const hasChild = parent.child.length > 0
				if (hasChild) {
					// Update In-Store and Mail-In enable status on parents child
					const { status } = parent.child.reduce(
						(curr, next) => {
							if (next.is_enable === 0) curr.status.enableAll = false
							if (next.is_enable === 1) curr.status.disableAll = false
							return curr
						},
						{
							status: {
								enableAll: true,
								disableAll: true
							}
						}
					)
					if (status.enableAll || !status.disableAll) {
						parent.is_enable = 1
						parent.instore = 1
						parent.mailin = 1
					}
					if (status.disableAll) {
						parent.is_enable = 0
						parent.instore = 0
						parent.mailin = 0
					}
				}
			}
		}
		const updateBuybackType = async (data, value, type) => {
			const validTypes = ['instore', 'mailin']
			const isInvalidType = !validTypes.includes(type)
			if (isInvalidType) return

			const { id } = data
			const payload = {
				type: null,
				category_id: null,
				category: titleCase(route.params.category),
				value: null
			}

			if (type === 'instore') {
				payload.type = 'instore'
				payload.category_id = id
				payload.value = value
			} else {
				payload.type = 'mailin'
				payload.category_id = id
				payload.value = value
			}

			const currentState = {...data}

			data[type] = data[type] == 2 && value == 1 ? 1 : value
			const isDisabledBothInstoreMailin =
				data.instore === 0 && data.mailin === 0
			if (isDisabledBothInstoreMailin) data.is_enable = 0

			const isParent = data.child.length > 0
			if (isParent) {
				updateBuybackTypeChildRow(data.child, type, value)
			}

			const flattenItem = flattenData.value.get(id)
			if (flattenItem) {
				if (flattenItem.parents)
					updateBuybackTypeParentRow(flattenItem.parents, type)
			}

			try{
				loading.buybackType.update.set(id, true)
				await actionUpdateBuybackType(payload)
			}catch(e){
				Object.assign(data, currentState)
				const isParent = data.child.length > 0
				if (isParent) {
					updateBuybackTypeChildRow(data.child, type, value == 1 ? 0 : 1)
				}

				const flattenItem = flattenData.value.get(id)
				if (flattenItem) {
					if (flattenItem.parents)
						updateBuybackTypeParentRow(flattenItem.parents, type)
				}
			}finally{
				loading.buybackType.update.delete(id)
			}
		}
		const showPricing = (row, type) => {
			router.push({
				path: `/setting/bulk-price-editor/${route.params.category}/${route.params.brand === 'all' ? row.manufacturer : route.params.brand}`,
				query: {
					ref: true,
					...(type === 2 && { model: row.name })
				}
			})
		}

		const flatten = (items = [], flattenItems = new Map(), parents = []) => {
			if (items?.length > 0) {
				for (let item of items) {
					flattenItems.set(item.id, { ...item, parents: parents })
					const hasChild = item?.child?.length > 0
					if (hasChild) flatten(item.child, flattenItems, [item, ...parents])
				}
			}
			return flattenItems
		}
		const normalizeKeys = (item) => ({
			...item,
			has_condition: item.has_condition ?? item.have_condition ?? 0,
			has_price: item.has_price ?? item.have_price ?? 0,
		})
		function enrichChildRecursive(items, parent, manufacturer) {
			return items.map(item => {
				const normalized = normalizeKeys(item)
				const hasChild = Array.isArray(normalized.child) && normalized.child.length > 0

				return {
					...normalized,
					has_child: hasChild ? 1 : 0,
					is_child: true,
					parent,
					manufacturer: item?.manufacturer ?? manufacturer,
					rawChild: hasChild ? markRaw(item.child) : [],
					_lazyFetched: false,
					child: []
				}
			})
		}

		const onExpandChange = async (row, node, resolve = (data) => data) => {
			row.is_loading = true

			// dummy expand
			if (!row._lazyFetched && row.rawChild?.length) {
				row.child = enrichChildRecursive(row.rawChild, row, row.manufacturer)
				row._lazyFetched = true
				row.is_loading = false
				resolve(row.child)
				return
			}

			let results = []
			await actionGetChildren({ id: row.id, data: { categories: route.params?.category }}, (res) => {
				results = enrichChildRecursive(JSON.parse(JSON.stringify(res ?? [])), row, row.manufacturer)
			})
			resolve(results)
			const index = data.value.findIndex(i => i.id === row.id)
			if (index !== -1) {
				const currentItem = data.value[index]
				data.value[index] = {
					...currentItem,
					is_loading: false,
					child: results
				}
			}
		}

		watch(
			() => props.data,
			() => {
				data.value = props.data.map(item => ({
					...item,
					child: item.child.map(itemchild => ({
						...itemchild,
						is_child: true,
						manufacturer: item.manufacturer
					}))
				}))
			},
			{
				deep: true
			}
		)

		watch(() => ({
			category: props.category
		}), (val) => {
			Object.assign(category, val.category)
		})
		watch(() =>( { name: category.name, param: paramsBrand.value}), () => {
			showTable.value = false
			setTimeout(() => {
				showTable.value = true
			}, 300)
		})

		return {
			category,
			data,
			flattenData,
			route,
			me,
			loading,
			updateBuybackDevice,
			updateBuybackDeviceByCategory,
			updateBuybackType,
			paramsBrand,
			showPricing,
			modal,
			dontShow,
			selectedRow,
			closeModal,
			shouldShowModal,
			findParentName,
			toggleEnable,
			onExpandChange,
			showTable
		}
	}
})
</script>
